














import Vue from 'vue'

export default Vue.extend({
  props: {
    active: { type: Boolean, required: true },
    rowIndex: { type: Number, required: true },
    columnIndex: { type: Number, required: true },
    content: String,
  },
  directives: {
    autofocus: {
      inserted(el) {
        el.focus()
      },
    },
  },
})
